html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}

section {
	padding: 30px 0;
}

@media (prefers-color-scheme: dark) {
    html, body {
		background: #121212;
        color: #eee;
    }

    a {
        color: #809fff;
    }
}
