// From responsivegridsystem.com/calculator/

/*  SECTIONS  */
.section {
	clear: both;
	padding: 0px;
	margin: 0px;
}

/*  COLUMN SETUP  */
.col {
	display: block;
	float: left;
	margin: 1% 0 1% 1.6%;
}
.col:first-child {
    margin-left: 0;
}

/*  GROUPING  */
.group:before,
.group:after {
    content:""; display: table;
}
.group:after {
    clear: both;
}
.group {
    /* For ancient browsers */
    zoom: 1;
}

/*  GRID OF THREE  */
.span_3_of_3 {
    width: 100%;
}
.span_2_of_3 {
    width: 66.13%;
}
.span_1_of_3 {
    width: 32.26%;
}

/* GRID OF TWO */

.span_2_of_2 {
	width: 100%;
}
.span_1_of_2 {
	width: 49.2%;
}

@media (max-width: 500px) {
	.col {
        margin: 1% 0 1% 0%;
    }
	.span_3_of_3,
    .span_2_of_3,
    .span_1_of_3,
    .span_1_of_2,
    .span_2_of_2 {
        width: 100%;
    }
}